import {main} from '../../state/i.js';


export default (
    (e) => {
        var
            t = e.currentTarget,
            r = t.getBoundingClientRect(),

            cx = e.clientX - r.left,
            cy = e.clientY - r.top,

            sc=0,

            cx_w2=cx-main.w2,
            cy_h2=cy-main.h2,

            x=main.x,
            y=main.y,

            before_x = ((cx_w2/(sc=main.scale))+x),
            before_y = ((cy_h2/sc)+y),

            after_x = ((cx_w2/(main.scale=sc*=(e.deltaY > 0 ? 1/1.15 : 1.15)))+x),
            after_y = ((cy_h2/sc)+y)
        ;
        
        return (
            e.preventDefault(),
            (main.x+=before_x-after_x),
            (main.y+=before_y-after_y),
            (main.dirty=true)
        );
    }
)